.header_btn {
  color: var(--white-color);
  background-color: var(--primary-color);
  border: 2px solid var(--primary-color);
  padding: 14px var(--spacing-large);
  font-weight: var(--bold-text);
  border-radius: 5px;
  cursor: pointer;
  transition: var(--transition);
}

.header_btn:hover {
  color: var(--primary-color);
  background-color: var(--white-color);
}

.discount_btn {
  height: 75px;
  border-radius: 25px;
  outline: none;
  border: none;
  font-size: 28px;
  font-weight: var(--bold-text);
  color: var(--white-color);
  background-color: transparent;
  border: 2px solid var(--white-color);
  cursor: pointer;
  transition: var(--transition);
}

.discount_btn:hover {
  background-color: var(--white-color);
  color: var(--primary-color);
}

.sale_btn {
  margin-top: 60px;
  padding: 25px 50px;
  border-radius: 15px;
  background-color: var(--white-color);
  border: 2px solid var(--white-color);
  font-size: 25px;
  color: var(--black-color);
  font-weight: var(--bold-text);
  cursor: pointer;
  transition: var(--transition);
}

.sale_btn:hover {
  background-color: var(--primary-color);
  color: var(--white-color);
}

.add_btn {
  position: absolute;
  bottom: -100%;
  left: 10px;
  width: 280px;
  height: 70px;
  background-color: #F1FFF1;
  border: 2px solid var(--primary-color);
  border-radius: 21px;
  color: var(--primary-color);
  font-weight: var(--medium-text);
  font-size: var(--sub-header-text);
  opacity: 1;
  transition: var(--transition);
  z-index: 3;
}

.add_btn:hover {
  background-color: var(--primary-color);
  color: var(--white-color);
  border: 2px solid var(--white-color);
}

.add_to_cart_btn {
  width: 340px;
  height: 86px;
  background-color: var(--primary-color);
  border-radius: 10px;
  color: var(--white-color);
  border: 2px solid var(--primary-color);
  color: var(--white-color);
  font-weight: var(--bold-text);
  font-size: 28px;
  transition: var(--transition);
  cursor: pointer;
}

.add_to_cart_btn:hover {
  background-color: var(--white-color);
  color: var(--primary-color);
}

.order_btn {
  background-color: var(--primary-color);
  height: 70px;
  border-radius: 20px;
  border: 2px solid var(--primary-color);
  color: var(--white-color);
  font-weight: var(--bold-text);
  font-size: 28px;
  transition: var(--transition);
  cursor: pointer;
}

.order_btn:hover {
  background-color: var(--white-color);
  color: var(--primary-color);
}

@media (max-width: 1180px) {
  .add_to_cart_btn {
    width: 200px;
    height: 50px;
  }
}

@media (max-width:990px) {
  .discount_btn {
    width: 100%;
  }
}

@media (max-width: 780px) {
  .header_btn {
    display: none;
  }
}

@media (max-width: 562px) {
  .order_btn {
    width: 100%;
  }
}

@media (max-width:400px) {

  .discount_btn,
  .order_btn {
    height: 50px;
  }
}